<template>
  <div id="cartgory" class="cartgory">
    <!-- Search搜索 -->
    <van-search v-model="searchData" disabled  placeholder="zhenhong" input-align="center" />

    <!-- TreeSelect 分类选择 -->
    <div class="cartgory-o">
      <van-tree-select
        :items="items"
        :main-active-index.sync="activeIndex"
        height="calc(100vh - 100px)"
        @click-nav="change"
      >
        <template #content>
          <div class="cartgorys" ref="cartgorys">
            <van-grid :column-num="2" icon-size="56px">
              <van-grid-item
                v-for="(item,index) in itemslist"
                :key="index"
                :icon="item.image"
                :text="item.title"
              />
            </van-grid>
            <tab-control
              class="tabcontrols"
              ref="tabcontrol2"
              @tabclick="tabclick"
              :titles="['流行','新款','精选']"
            ></tab-control>
            <good-list :goodlist="showgood"></good-list>
          </div>
        </template>
      </van-tree-select>
    </div>
  </div>
</template>
<script>
import {
  getcategory,
  getcategoryitem,
  getsubcategoryitem
} from "../../network/category";
import TabControl from "../../components/content/TabControl";
import GoodList from "../../components/content/goods/GoodList";

export default {
  name: "Cartgory",
  components: {
    TabControl,
    GoodList
  },
  data() {
    return {
      searchData: "",
      activeIndex: 0,
      items: [],
      itemslist: [],
      currentindex: "pop",
      goods: {
        pop: { list: [] },
        new: { list: [] },
        sell: { list: [] }
      },
      top: ""
    };
  },
  computed: {
    //计算属性
    showgood() {
      return this.goods[this.currentindex].list;
    }
  },
  created() {
    // 获取 分类导航数据
    this.getcategory();

    //请求商品数据
    // this.getsubcategoryitem("pop")
    // this.getsubcategoryitem("new")
    // this.getsubcategoryitem("sell")
  },
  methods: {
    //事件监听
    tabclick(index) {
      switch (index) {
        case 0:
          this.currentindex = "pop";
          break;
        case 1:
          this.currentindex = "new";
          break;
        case 2:
          this.currentindex = "sell";
      }

      let miniWallkey = this.items[this.activeIndex].miniWallkey;
      this.getsubcategoryitem(this.currentindex, miniWallkey);
    },
    //监听事件
    change(index) {
      this.activeIndex = index;
      this.getcategoryitem();
      let miniWallkey = this.items[this.activeIndex].miniWallkey;
      this.getsubcategoryitem(this.currentindex, miniWallkey);
    },

    // 获取 分类导航数据
    getcategory() {
      getcategory()
        .then(res => {
          // 
          let data = res.data.data.category.list;
          data.forEach(element => {
            // 
            element.text = element.title;
            this.items.push(element);
          });
          let miniWallkey = this.items[this.activeIndex].miniWallkey;
          this.getcategoryitem();
          this.getsubcategoryitem(this.currentindex, miniWallkey);
        })
        .catch(err => {
          
        });
    },

    //获取分类数据
    getcategoryitem() {
      let maitKey = this.items[this.activeIndex].maitKey;
      // 
      getcategoryitem(maitKey)
        .then(res => {
          this.itemslist = res.data.data.list;
        })
        .catch(err => {
          
        });
    },

    //获取分类详情数据
    getsubcategoryitem(type, miniWallkey) {
      getsubcategoryitem(type, miniWallkey)
        .then(res => {
          // 
          this.goods[type].list = res.data;
        })
        .catch(err => {
          
        });
    }
  }
};
</script>
<style lang="less" scoped>
.cartgory {
  .van-search {
    display: flex;
    padding: 8px 12px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    box-shadow: 0 -1px 1px rgba(100, 100, 100, 0.2);
    z-index: 9;
  }
  .van-tree-select {
    margin-top: 50px;
  }
}
</style>
