<template>
  <div class="goods-item" @click="godetail">
    <img class="auto-img" v-lazy="showimage" alt="" @load="imageload">
    <div class="goods-info">
      <p>{{gooditem.title}}</p>
      <span class="price">{{gooditem.price}}</span>
      <span class="collect">{{gooditem.cfav}}</span>
    </div>
  </div>
</template>
<script>

export default {
    props:{
        gooditem:{
            type:Object,
            default(){
                return {}
            }
        }
    },
    computed:{
      showimage(){
        return this.gooditem.image  || this.gooditem.img || this.gooditem.show.img
      }
    },
    methods:{
      imageload(){
        // 
        if(this.$route.path.indexOf("/home") ==0){
          // 
          
            this.$bus.$emit("imageload")
        }else if(this.$route.path.indexOf("/detail") ==0){
            this.$bus.$emit("LoadImage")
            // 
        }
        
      },
      godetail(){
        if(this.$route.path.indexOf("/home")==0){
          this.$router.push({name:'Detail',query:{id:this.gooditem.iid}})
        }else if(this.$route.path.indexOf("/detail") ==0){
        }else if(this.$route.path.indexOf("/cartgory") ==0){
          this.$router.push({name:'Detail',query:{id:this.gooditem.iid}})
        }
        
      }
    }
}
</script>
<style lang="less" scoped>
.goods-item{
  padding-bottom: 50px;
  position: relative;
  width: 48%;
}
  .goods-item img{
    width: 100%;
    border-radius: 5px;
  }
  .goods-info{
    font-size: 14px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    overflow: hidden;
    // text-align: center;
  }
  .goods-info p{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
  }
  .goods-info .price{
    color: salmon;
  }
  .goods-info .collect{
    position: relative;
    margin-left: 30px;
  }
  .goods-info .collect::before{
    content: "";
    position: absolute;
    left: -15px;
    top: 0px;
    width: 15px;
    height: 15px;
    background: url("../../../assets/img/star_active.png") no-repeat;
    background-size: 100%;
  }
</style>